import React from "react";
import {Formik} from "formik";
// @ts-ignore
import {Datepicker, Form} from 'react-formik-ui';
import s from './SetLastDateForm.module.css'

const validateDate = (values: string | undefined | null) => {
    let error;
    if (!values) {
        error = 'Required';
    }
    return error;
}


export const SetLastDateForm = React.memo((props: any) => {

    const submit = (values: any, {setSubmitting}: { setSubmitting: (isSubmitting: boolean) => void }) => {
        let error;
        if (values.fullDate === '') {
            error = 'Required';
            return error;
        } else {
            let JSONDate = JSON.stringify(values)
            let fullDate = JSONDate.slice(13, 23)
            props.setlastDate(fullDate)
            setSubmitting(false)
        }
    }


    return (
        <Formik
            initialValues={{fullDate: ''}}
            // @ts-ignore
            onSubmit={submit}
        >
            {({isSubmitting, errors}) => (
                <Form className={s.setDateForm}>
                    <Datepicker
                        name='fullDate'
                        label='Load messages from'
                        placeholder='DD.MM.YYYY'
                        validate={validateDate}
                    />
                    {errors.fullDate &&  <div>{errors.fullDate}</div>}
                    <button className={s.setDateBtn} type="submit" disabled={isSubmitting}>
                        Set date
                    </button>
                </Form>
            )}
        </Formik>
    )
})

