import React from 'react';

export const groupBy = (objectArray: Array<any>, property: any) => {
    return objectArray.reduce(function (acc: any, obj: any) {
        var key = obj[property].slice(0, 10);
        if (!acc[key]) {
            acc[key] = [];
        }
        acc[key].push(obj);
        return acc;
    }, {});
}