import {AppStateType} from "./redux-store";

export const getUsers = (state: AppStateType) => {
    return state.dialogsPage.users
};
export const getIsFetching = (state: AppStateType) => {
    return state.dialogsPage.isFetching
};
export const getPortionSize = (state: AppStateType) => {
    return state.dialogsPage.portionSize
};
export const getMessages = (state: AppStateType) => {
    return state.dialogsPage.messages
};
export const getLastDate = (state: AppStateType) => {
    return state.dialogsPage.lastDate
};
export const getCurrentChat = (state: AppStateType) => {
    return state.dialogsPage.currentChat
};
export const getMessagesTotalCount = (state: AppStateType) => {
    return state.dialogsPage.totalCount
};
