import React, {useState} from 'react';
import s from './DialogChat.module.css';
import {Message} from "./Message/Message";
import {FormType} from "../common/FormsControls/FormsControls";
import {maxLengthCreator, required} from "../../utils/validators/validators";
import {Field, reduxForm} from "redux-form";
import {Preloader} from "../common/Preloader/Preloader";
import {useDispatch, useSelector} from "react-redux";
import {actions, deleteMessage, requestOldMessages, sendMessage} from "../../redux/dialogs-reducer";
import {
    getCurrentChat,
    getLastDate,
    getMessages,
    getMessagesTotalCount,
    getPortionSize
} from "../../redux/dialogs-selectors";
import {getAuthorizedUserId} from "../../redux/auth-selectors";
import {groupBy} from "../common/Grouper/Grouper.tsx";
import {splitMessageDate} from "../common/DateControls/DateControls";
import {getProfile} from "../../redux/profile-selectors";
import {DialogsUserType} from "../../types/types";
import {SetLastDateForm} from "./SetLastDateForm";
import classNames from "classnames";

type PropsType = {
    users: Array<DialogsUserType>
}

export const DialogChat: React.FC<PropsType> = (props) => {
    const portionSize = useSelector(getPortionSize)
    const messages = useSelector(getMessages)
    const lastDate = useSelector(getLastDate)
    const currentChat = useSelector(getCurrentChat)
    const authUserId = useSelector(getAuthorizedUserId)
    const authUserInfo = useSelector(getProfile)
    const totalCount = useSelector(getMessagesTotalCount)
    const dispatch = useDispatch();

    const [formState, setForm] = useState('active')

    const deleteChatMessage = (messageId: string) => {
        dispatch(deleteMessage(messageId))
    }

    const [portionNumber, setPortionNumber] = useState(1)
    const loadNewPortion = portionNumber * portionSize;
    let messagePortion = [];
    let messagesElements = [] as Array<any>


    let getOldMessages = (id: number, date: string) => {
        dispatch(requestOldMessages(id, date))
        setPortionNumber(portionNumber + 1)
    }
    if (messages.length) {
        for (let i = 0; i < messages.length && i < loadNewPortion; i++) {
            messagePortion.push(messages[i])
        }
        messagePortion = groupBy(messagePortion, 'addedAt')

        for (let elem in messagePortion) {
            messagePortion[elem].push({
                addedAt: elem,
                body: splitMessageDate(elem),
            })

        }
        for (let elem in messagePortion) {
            messagePortion[elem].map((e: any) => messagesElements.push(e))
        }

        messagesElements = messagesElements.map((m: any) => (
            <Message message={m.body} key={m.id ? m.id : m.body} date={m.addedAt} senderName={m.senderName}
                     viewed={m.viewed}
                     id={m.id} senderId={m.senderId} recipientId={m.recipientId} authUserId={authUserId}
                     authUserInfo={authUserInfo} deleteMessage={deleteChatMessage}/>
        ))
    }

    let addNewMessage = (values: any) => {
        dispatch(sendMessage(currentChat as number, values.newMessageBody))
        values.newMessageBody = null;
    };

    let handleScroll = (e: any) => {
        let element = e.target
        if (element.scrollTop === 0) {
            getOldMessages(currentChat as number, lastDate)
        }
    }

    const setlastDate = (datenow: any) => {
        dispatch(actions.setlastDate(datenow));
        dispatch(requestOldMessages(currentChat as number, datenow));
        setPortionNumber(portionNumber + 9)
    }

    return (
        <div className={s.dialogsPage}>

            {totalCount as number > 9 ? formState === 'active' ?
                <button className={s.toggleDateForm} onClick={() => setForm('passive')}>Hide form</button>
                : <button className={s.toggleDateForm} onClick={() => setForm('active')}>Show form</button>
                : null}

            {messagePortion ? <div onScroll={handleScroll} className={s.chat}>
                {totalCount as number > 9 ? <div className={classNames(s.SetLastDateForm, s[formState])}>
                    <div className={s.SetLastDateFormInner}>
                        Only the last 10 messages receive with get-request. If there are
                        more than ten you can use this form
                        <SetLastDateForm setlastDate={setlastDate}/>
                    </div>
                </div> : null}
                <div className={s.messages}>{messagesElements}</div>
                <AddMessageFormRedux onSubmit={addNewMessage}/></div> : <Preloader/>}
        </div>
    );
}
const Textarea = FormType('textarea');
const maxLength150 = maxLengthCreator(150);

const AddMessageForm = (props: any) => {
    return (
        <form className={s.messageForm} onSubmit={props.handleSubmit}>
            <div>
                <Field
                    component={Textarea}
                    validate={[required, maxLength150]}
                    name="newMessageBody"
                    placeholder="Enter your message"
                />
            </div>
            <div>
                <button>Add Message</button>
            </div>
        </form>
    );
}

const AddMessageFormRedux = reduxForm({
    form: 'dialogAddMessageForm',
})(AddMessageForm);


