import {FormType} from "../FormsControls/FormsControls";
import {maxLengthCreator, required} from "../../../utils/validators/validators";
import {Field, reduxForm} from "redux-form";
import React from "react";
import s from './UsersMessageForm.module.css'
import userPhoto from "../../../assets/images/User.jpg";
import {Redirect, withRouter} from "react-router-dom";
import {compose} from "redux";

const Textarea = FormType('textarea');
const maxLength100 = maxLengthCreator(100);

const AddMessageUserForm = (props: any) => {
    return (
        <div className={s.UserMessageFormWindow}>
            <div className={s.UserMessageForm}>

                <div className={s.UserMessageFormHeader}>
                    <div className={s.UserMessageFormTitle}>New message</div>
                    <div className={s.UserMessageFormHeaderButtons}>
                        <div className={s.UserMessageFormRedirectBtn}
                             onClick={() => props.history.push(`/dialogs`)}>Redirect to
                            dialogs
                        </div>
                        <div onClick={() => props.setFormControl(false)} className={s.UserMessageFormCloseBtn}>&times;
                        </div>
                    </div>
                </div>
                <div className={s.UserMessageFormItem}>
                    <div>
                        <img className={s.UserMessageFormImg} src={props.photo != null ? props.photo : userPhoto}/>
                    </div>
                    <div className={s.UserMessageFormInfo}>
                        <div className={s.UserMessageFormReceiver}>{props.receiver}</div>
                    </div>
                </div>
                <form onSubmit={props.handleSubmit}>
                    <div className={s.UserMessageFormTextare}>
                        <Field
                            component={Textarea}
                            validate={[required, maxLength100]}
                            name="newMessageBody"
                            placeholder="Enter your message"
                        />
                    </div>
                    <div className={s.UserMessageFormFooter}>
                        <button className={s.UserMessageFormAddMessage}>Add Message</button>
                    </div>
                </form>
            </div>
        </div>
    );
};

export const AddMessageUserFormRedux: any = compose(
    reduxForm({
        form: 'userAddMessageForm',
    }),
    withRouter
)(AddMessageUserForm);
