import React from "react";

export const formattedDate = (date: string) => {
    return date.slice(0, 10)

}
export const formattedTime = (time: string) => {
    let newTime = time.slice(11, 16)
    let hour = +newTime[1]
    hour = hour + 3
    const regexp = /.\d/
    return newTime.replace(regexp, `${hour}`)
}
export const splitMessageDate = (date: any) => {
    let year = date.slice(0, 4)
    let month = date.slice(5, 7)
    month = replaceMonth(month)
    let day = date.slice(8, 10)
    if (year === '2020') {
        return `${day} ${month}`
    } else {
        return `${day} ${month} ${year}`
    }

}

const replaceMonth = (month: string) => {
    switch (month) {
        case '01': {
            return month = 'January'
        }
        case '02': {
            return month = 'February'
        }
        case '03': {
            return month = 'March'
        }
        case '04': {
            return month = 'April'
        }
        case '05': {
            return month = 'May'
        }
        case '06': {
            return month = 'June'
        }
        case '07': {
            return month = 'July'
        }
        case '08': {
            return month = 'August'
        }
        case '09': {
            return month = 'September'
        }
        case '10': {
            return month = 'October'
        }
        case '11': {
            return month = 'November'
        }
        case '12': {
            return month = 'December'
        }
    }
    return month
}