import React from 'react';
import s from './ProfilePosts.module.css'
import heart from '../../../../assets/icons/heart.png';
import view from '../../../../assets/icons/view.png';

type PropsType = {
    photo: string
    addedAt: string
    senderName: string
    body: string
    likesCount: number
    views: number
    id: number

    addLike: (id: number, likesCount: number) => void
}

export const Post: React.FC<PropsType> = (props) => {
    return (
        <div className={s.Post}>
            <div className={s.postItem}>
                <div className={s.photo}><img src={props.photo}/></div>
                <div className={s.postInfo}>
                    <div className={s.senderName}>{props.senderName}</div>
                    <div className={s.addedAt}>{props.addedAt}</div>
                </div>
            </div>
            <div className={s.postBody}>
                {props.body}
            </div>
            <div className={s.postFooter}>

                <div className={s.likesCount}><img onClick={() => props.addLike(props.id, props.likesCount + 1)}
                                                   src={heart}/> {props.likesCount}</div>
                <div className={s.views}><img src={view}/> {props.views}</div>
            </div>
        </div>
    )

}