import React, {useState} from 'react';
import s from './ProfileInfo.module.css';
import styles from "../../common/FormsControls/FormsControls.module.css";
import {FormType, createField, GetStringKeys} from "../../common/FormsControls/FormsControls";
import {InjectedFormProps, reduxForm} from "redux-form";
import {ProfileType} from "../../../types/types";
import classNames from "classnames";


const Input = FormType('input');
const Textarea = FormType('textarea');

type PropsType = {
    profile: ProfileType
    isFetching: boolean
}

type ProfileTypeKeys = GetStringKeys<ProfileType>

const ProfileDataForm: React.FC<InjectedFormProps<ProfileType, PropsType> & PropsType> =
    ({handleSubmit, profile, error, isFetching}) => {

        let [activeClass, setActiveClass] = useState(false);
        return (
            <form className={s.profileDataForm} onSubmit={handleSubmit}>
                {error && <div className={styles.formSummaryError}>{error}</div>}
                <div>
                    {isFetching ? <button disabled>save</button>
                        : <button className={s.goToEditMode}>save</button>
                    }
                </div>
                <div className={s.fullNameForm}>
                    <b className={s.dataTitle}>Full
                        name:</b> {createField<ProfileTypeKeys>('Full name', 'fullName', [], Input)}
                </div>
                <div className={s.lookingForAJob}>
                    <b className={s.dataTitle}>Looking for a job:</b>
                    {createField<ProfileTypeKeys>('', 'lookingForAJob', [], Input, {type: 'checkbox'})}
                </div>

                <div className={s.mySkills}>
                    <b className={s.dataTitle}>My professional skills:</b>
                    {createField<ProfileTypeKeys>('My professional skills', 'lookingForAJobDescription', [], Textarea)}
                </div>

                <div className={s.aboutMe}>
                    <b className={s.dataTitle}>About me:</b>
                    {createField<ProfileTypeKeys>('About me', 'aboutMe', [], Textarea)}
                </div>
                <div className={s.contactsMenu}>
                    <div className={s.contactsTitle} onClick={() => setActiveClass(!activeClass)}>{
                        activeClass ? 'Hide Contacts' : 'Show Contacts'
                    }</div>
                    {activeClass ? <div className={classNames(s.contactsContent, s.active)}>
                            {Object.keys(profile.contacts).map(key => {
                                return <div key={key} className={s.contact}>
                                    <b className={s.dataTitle}>{key}:</b> {createField(key, 'contacts.' + key, [], Input)}
                                </div>
                            })} </div>

                        : <div className={classNames(s.contactsContent, s.passive)}>
                            {Object.keys(profile.contacts).map(key => {
                                return <div key={key} className={s.contact}>
                                    <b className={s.dataTitle}>{key}:</b> {createField(key, 'contacts.' + key, [], Input)}
                                </div>
                            })}
                        </div>}

                </div>
            </form>
        )
    }

const ProfileDataReduxForm = reduxForm<ProfileType, PropsType>({
    form: 'edit-profile',
})(ProfileDataForm)


export default ProfileDataReduxForm;