import React, {useEffect} from 'react';
import s from './ProfilePosts.module.css'
import {useDispatch, useSelector} from "react-redux";
import {getPosts} from "../../../../redux/profile-selectors";
import {Post} from "./Post";
import {Field, Form, Formik} from "formik";
import {PostType} from "../../../../types/types";
import {actions} from "../../../../redux/profile-reducer";
import {FormType} from "../../../common/FormsControls/FormsControls";
import {randomInteger} from "../../../common/randomInteger/randomInteger";

type PropsType = {
    photo: string
    senderName: string
    isOwner: boolean
}

export const ProfilePosts: React.FC<PropsType> = (props) => {

    const posts = useSelector(getPosts)
    const dispatch = useDispatch()

    const addLike = (id: number, likesCount: number) => {
        dispatch(actions.addLike(id, likesCount))
    }

    return (
        <div className={s.PostData}>
            {props.isOwner ? <div className={s.PostForm}>
                <AddPostFrom posts={posts}/>
            </div> : null}
            <div className={s.Posts}>
                {posts.map((p) => <Post body={p.body} photo={props.photo} addedAt={p.addedAt}
                                        senderName={props.senderName} likesCount={p.likesCount}
                                        views={p.views} id={p.id} addLike={addLike}/>).reverse()}
            </div>
        </div>
    )

}
const Textarea = FormType('textarea');
const AddPostFrom = React.memo((props: any) => {
    const dispatch = useDispatch()

    let randomNum = randomInteger(1, 1000)

    useEffect(() => {
        randomNum = randomInteger(1, 1000)
    }, [props.posts.length])


    const submit = (values: any, {setSubmitting}: { setSubmitting: (isSubmitting: boolean) => void }) => {
        const newPost: PostType = {
            addedAt: '00-00-0000',
            likesCount: 0,
            views: 1,
            id: randomNum,
            body: values.post,
            senderId: 1000,
        }
        dispatch(actions.addPost(newPost))
        setSubmitting(false)
    }

    return (
        <Formik
            initialValues={{post: ''}}
            onSubmit={submit}
        >
            {({isSubmitting}) => (
                <Form>
                    <Field component={'textarea'} name="post"/>
                    <button type="submit" disabled={isSubmitting}>
                        Add Post
                    </button>
                </Form>
            )}
        </Formik>
    )
})