import {GetItemsType, instanse, APIResponseType} from "./api";


export const UsersAPI = {
    getUsers(currentPage = 1, pageSise = 10, term = '', friend: null | boolean = null) {
        return instanse
            .get<GetItemsType>(`users?page=${currentPage}&count=${pageSise}&term=${term}` + (friend === null ? '' : `&friend=${friend}`))
            .then(res => res.data);
    },
    follow(userId: number) {
        return instanse.post<APIResponseType>(`follow/${userId}`).then(res => res.data)
    },
    unfollow(userId: number) {
        return instanse
            .delete(`follow/${userId}`).then(res => res.data) as Promise<APIResponseType>
    },
};