import {PhotosType, ProfileType} from "../types/types";
import {instanse, APIResponseType} from "./api";

type SavePhotoResponseDataType = {
    photos: PhotosType
}

export const ProfileAPI = {
    getProfile(userId: number) {
        return instanse.get<ProfileType>(`profile/${userId}`).then(res => res.data);
    },
    getStatus(userId: number) {
        return instanse.get<string>(`profile/status/` + userId).then(res => res.data);
    },
    updateStatus(status: string) {
        return instanse.put<APIResponseType>(`profile/status`, {status: status}).then(res => res.data);
    },
    savePhoto(photoFIle: File) {
        const formData = new FormData();
        formData.append('image', photoFIle)
        return instanse.put<APIResponseType<SavePhotoResponseDataType>>(`profile/photo`, formData).then(res => res.data);
    },
    saveProfile(profile: ProfileType) {
        return instanse.put<APIResponseType>(`profile`, profile).then(res => res.data);
    }
};